$people-lines-height: 130, 205, 165, 110, 215, 165, 65, 120, 190;
$people-lines-left-pos: 75, 190, 290, 390, 505, 618, 713, 812, 925;
$people-lines-margin-mobile: 15, 17, 15, 13, 16, 20, 14, 13, 17;
.people-group {
  position: relative;
  padding-top: percentage(230/990);
  max-width: rem(990);
  margin: rem(80) auto 0;
  .people {
    position: relative;
    z-index: 1; }
  @include sm {
    margin-top: rem(60);
    padding: 0;
    max-width: none;
    .people {
      font-size: rem(14);
      position: absolute;
      top: rem(-6);
      right: 0;
      @include responsiveImgNoRetina('people-mobile', 128, 360); }
    .people-group__line {
      position: relative;
      left: 0;
      bottom: 0;
      width: auto;
      height: auto;
      background: none;
      &:before {
        content: "";
        position: absolute;
        z-index: 0;
        top: 50%;
        left: -100px;
        right: 100px;
        height: 1px;
        background: $color-line; } }
    .people-group__label {
      position: relative;
      z-index: 1;
      margin: 0;
      width: auto;
      left: initial; } }
  @include r(359) {
    .people {
      font-size: rem(13.7); } } }

.people-group__line {
  position: absolute;
  z-index: 0;
  bottom: percentage(230/600);
  left: 0;
  width: 1px;
  background: $color-line;
  height: rem(100);
  @for $i from 1 through 9 {
    &:nth-child(#{$i}) {
      left: percentage((nth($people-lines-left-pos, $i)) / 990);
      height: percentage((nth($people-lines-height, $i) + 100) / 600); } }
  @include sm {
    @for $i from 1 through 9 {
      &:nth-child(#{$i}) {
        margin-top: rem(nth($people-lines-margin-mobile, $i)); } } } }

.people-group__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: rem(150);
  margin: 0 0 rem(20) rem(-75);
  text-align: center;
  white-space: nowrap; }
