.back-btn-group {
  position: relative;
  margin-bottom: rem(50);
  @include f(m-regular, 10, 10, 250);
  text-transform: uppercase;
  transition: color 0.5s ease;
  a {
    position: relative;
    z-index: 1;
    top: em(1,10);
    display: inline-block;
    vertical-align: top;
    color: inherit;
    text-decoration: none; }
  .arrow-line {
    position: absolute;
    z-index: 0;
    top: 50%;
    left: 0;
    right: 0; }
  &:hover {
    color: $color-gradient; }
  @include sm {
    .arrow-line {
      right: -$outer-padding-sm; } } }
