.footer {
  background: $color-text-main;
  color: $color-text-grey-darker;
  padding-top: rem(60);
  padding-bottom: rem(40);
  font-size: rem(12);
  a:not([class]) {
    color: white;
    text-decoration: none; }
  .quote {
    margin-top: rem(25);
    font-size: inherit; }
  .quote__cite {
    margin-top: em(5); }
  @include sm {
    font-size: inherit; } }

.footer__row {
  display: flex;
  justify-content: space-between;
  margin-top: rem(70);
  &:first-child {
    margin-top: 0; }
  @include sm {
    flex-direction: column;
    &:first-child {
      .footer__left {
        order: 2;
        margin-top: rem(70); }
      .footer__rigth {
        order: 1; } }
    &:nth-child(2) {
      .footer__right {
        margin-top: rem(30); } } } }

.footer__left {
  width: 50%;
  @include sm {
    width: auto; } }

.footer__right {
  width: 50%;
  max-width: 430px;
  @include sm {
    width: auto;
    max-width: none; } }

.footer__contacts {
  font-size: rem(14);
  @include sm {
    font-size: rem(16);
    margin-top: rem(70);
    li {
      margin-top: rem(10); } } }

.footer__nav {
  margin-bottom: rem(25);
  li {
    margin-bottom: rem(5); }
  @include sm {
    li {
      margin-bottom: rem(30); }
    .link {
      font-size: rem(14);
      white-space: normal;
      text-align: left;
      color: white; }
    .link__hover-wrap {
      display: none; } } }

.footer__copyright {
  display: inline-block;
  vertical-align: middle;
  margin-right: rem(25);
  @include sm {
    display: block;
    margin: 0;
    font-size: rem(12); } }

.footer__title {
  color: white;
  @include f(m-regular);
  margin-bottom: em(5); }

.footer__design-by {
  color: $color-text-grey;
  @include f(m-regular, 12);
  a {
    margin-left: 0.5em;
    @include f(m-bold, 10, 10, 500);
    color: white;
    text-decoration: none;
    &:hover {
      text-decoration: none; } } }
