.bubbles-group {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: em(1130);
  height: em(540);
  .h3-like {
    display: none;
    margin-bottom: rem(40); }
  .network-3 {
    position: absolute;
    bottom: em(-45);
    left: 50%;
    margin-left: em(-90);
    transform: translate(-50%, 0); }
  .bubble {
    position: absolute;
    width: em(320);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    br {
      display: none; }
    &:first-child {
      margin: em(-240) 0 0 em(-40); }
    &:nth-child(2) {
      margin: em(-92) 0 0 em(325); }
    &:nth-child(4) {
      margin: em(143) 0 0 em(400); }
    &:nth-child(3) {
      margin: em(-40) 0 0 em(-427); }
    &:nth-child(5) {
      margin: em(208) 0 0 em(-410); } }
  .bubble__avatar {
    display: none; }
  @include r(1280) {
    font-size: rem(13); }
  @include r(1100) {
    font-size: rem(11); }
  @include md {
    font-size: inherit;
    margin: rem(50) 0 0;
    max-width: none;
    height: auto;
    .network-3 {
      display: none; }
    .h3-like {
      display: block;
      height: auto;
      max-width: none; }
    .bubble {
      position: relative;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      transform: none;
      &:nth-child(n) {
        margin: rem(25) 0 0 0; } }
    .bubble__avatar {
      display: block; } } }

.bubble {
  padding: em(15) em(18);
  border-radius: em(10);
  border: 1px solid #EEEEEE;
  color: $color-text-grey-darker;
  background: white; }

.bubble__avatar {
  position: absolute;
  display: none;
  top: 50%;
  right: 100%;
  margin-right: rem(10);
  margin-top: rem(-15);
  width: rem(30);
  height: rem(30);
  border-radius: 50%;
  background: $color-line no-repeat center;
  background-size: cover;
  overflow: hidden;
  img {
    display: block;
    max-width: 100%;
    max-height: 100%; } }
