.video-container {
  position: relative;
  padding: 30px 0 0;
  background: $color-text-main no-repeat center;
  background-size: cover;
  color: white;
  height: rem(400);
  overflow: hidden;
  box-sizing: border-box;
  > iframe {
    position: relative;
    z-index: 0;
    cursor: none; }
  .play-btn {
    display: none;
    margin-top: rem(40); }
  .video-poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: no-repeat center;
    background-size: cover;
    transition: transform 0.5s ease;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: black;
      opacity: 0.6; } }
  &--darker {
    .video-poster {
      &:before {
        opacity: 0.8; } } }
  &.is-ready {
    cursor: img-url('cursors/play.png'), pointer;
    &:hover:not(.is-active) {
      .video-poster {
        transform: scale(1.1); } } }
  &.is-active {
    background: black;
    cursor: auto;
    .video-container__content {
      transform: translate(-100%, -50%);
      opacity: 0;
      visibility: hidden; } }
  &.is-opened {
    .video-container__close {
      opacity: 1;
      visibility: visible; } }
  .touch-device & {
    .play-btn {
      display: block; } }
  @include sm {
    height: rem(340);
    .play-btn {
      display: block; } } }

.video-container__content {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: $inner-padding + $outer-padding;
  transform: translate(0, -50%);
  margin-top: -0.5px;
  transition: all 0.5s ease;
  padding-right: 30px;
  @include md {
    left: $inner-padding-md + $outer-padding-md; }
  @include xs {
    .subtitle {
      br {
        display: none; } } } }

.video-container__close {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px; }

.play-btn {
  display: inline-block;
  width: rem(50);
  height: rem(50);
  border-radius: 50%;
  background: white;
  color: $color-text-main;
  text-align: center;
  &:before {
   content: "";
   display: block;
   margin: auto;
   @include arr(10,12,currentColor,r);
   position: relative;
   left: 1px; } }
