html {
  font-size: 62.5%;
  @include rmin(1336) {
    font-size: 0.732vw; }
  @include r(359) {
    font-size: 9px; } }


body {
  min-width: $min-width;
  background: white;
  color: $color-text-main;
  @include f(p-regular, 14, 20);
  @include smooth-font;
  @include sm {
    font-size: rem(17); } }

.out {
  overflow: hidden; }

.outer-wrap {
  padding: 0 $outer-padding;
  @include md {
    padding-left: $outer-padding-md;
    padding-right: $outer-padding-md; } }

.inner-wrap {
  padding-left: $inner-padding;
  @include md {
    padding-left: $inner-padding-md; } }

.justify {
  display: flex;
  justify-content: space-between; }

.section {
  position: relative;
  margin: rem(80) 0;
  &:first-of-type {
    margin-top: rem(40); }
  > p {
    margin: 1.5em 0; }
  @include sm {
    > p {
      br {
        display: none; } } }
  &--sub {
    .section__number {
      color: $color-text-grey; } }
  &--video {
    height: rem(400);
    @include sm {
      height: rem(340); } }
  &--1-2-1 {
    margin: rem(210) 0;
    .silouettes {
      bottom: 50%;
      margin-bottom: rem(-400); }
    .arrow {
      margin-top: rem(170); }
    @include md {
      margin-top: rem(80);
      .silouettes {
        margin-bottom: rem(-350); }
      .arrow {
        margin: rem(130) 0 0 rem(50); } }
    @include sm {
      margin: rem(80) 0 rem(360);
      .list {
        position: relative;
        z-index: 1; }
      .silouettes {
        z-index: 0;
        bottom: 0;
        margin-bottom: rem(-190);
        margin-right: rem(-200);
        width: rem(450); }
      .arrow {
        margin: rem(90) 0 0 rem(-70); } }
    @include xs {
      .silouettes {
        margin-right: rem(-255); } } }
  &--1-2-2 {
    // .arrow-3
    .arrow {
      margin: rem(-80) 0 0 rem(120); }
    @include md {
      .arrow {
        margin: rem(-40) 0 0 rem(150); } }
    @include sm {
      .arrow {
        // width: rem(18)
        width: rem(34);
        margin: rem(-55) 0 0 rem(80); } } }
  &--1-3-1 {
    // .arrow-4
    .arrow {
      margin: rem(-30) 0 0 rem(50); }
    @include r(1100) {
      .arrow {
        margin-left: 0; } }
    @include sm {
      margin-bottom: rem(180);
      .arrow {
        margin: rem(-35) 0 0 rem(-90); } } }
  &--1-3-3 {
    .list {
      margin-top: rem(27); }
    @include sm {
      .list-item {
        .h4-like {
          display: block;
          margin-bottom: 1em; }
        &:before {
          top: 0.5em; } } } }

  &--2-2 {
    @include min-md {
      margin-top: rem(190); } }
  &--2-2-1 {
    margin-bottom: rem(150);
    .silouettes {
      bottom: rem(-42); }
    // .arrow-1
    .arrow {
      z-index: 2;
      margin: rem(70) 0 0 rem(-70); }
    @include md {
      .silouettes {
        width: 60%; } }
    @include sm {
      margin-bottom: rem(200);
      .silouettes {
        z-index: 0;
        bottom: 0;
        margin-bottom: rem(-80);
        margin-right: rem(-200);
        width: rem(450); }
      .list {
        position: relative;
        z-index: 1; }
      .arrow {
        width: rem(190);
        margin: rem(40) 0 0 rem(-110); } }
    @include xs {
      .silouettes {
        margin-right: rem(-255); } } }
  &--2-2-2 {
    .arrow {
      margin: rem(-80) 0 0 rem(150); }
    @include md {
      .arrow {
        width: rem(18);
        margin: rem(-60) 0 0 rem(180); } }
    @include sm {
      margin-bottom: rem(150);
      .arrow {
        width: rem(34);
        margin: rem(-80) 0 0 rem(50); } } }

  &--3-2-1 {
    margin: rem(160) 0;
    // .arrow-5
    .arrow {
      margin: rem(50) 0 0; }
    @include sm {
      margin: rem(80) 0 rem(200);
      .arrow {
        margin: rem(25) 0 0 rem(-70);
        width: rem(70); } } }

  &--3-2-2 {
    // .arrow-5
    .arrow {
      margin-top: rem(-50); }
    @include r(1200) {
      .arrow {
        margin-left: rem(100); } }
    @include sm {
      margin-bottom: rem(160);
      .arrow {
        width: rem(75);
        margin: rem(-60) 0 0 rem(-20); } } }
  &--3-3-1 {
    margin-bottom: rem(160);
    // .arrow-6
    .arrow {
      margin: rem(100) 0 0 rem(200); }
    @include sm {
      margin-bottom: rem(100);
      .arrow {
        display: none; } } } }

.section__number {
  position: absolute;
  top: 0;
  left: -$inner-padding;
  @include f(m-semibold, 12, 18);
  @include md {
    left: -$inner-padding-md;
    > span {
      display: block; } } }

.silouettes {
  position: absolute;
  bottom: rem(-42);
  right: 0;
  z-index: 1;
  @include responsiveImg('silouettes', 623, 727);
  width: 55%; }

.network-1 {
  margin: rem(-100) auto;
  @include responsiveImg('network1', 1073, 758, 'jpg');
  @include sm {
    margin: 0 auto 0 -165px;
    margin-left: -165px;
    width: calc(100% + 450px); } }

.network-2 {
  margin: rem(-50) auto;
  @include responsiveImg('network2', 1073, 764);
  @include sm {
    margin: rem(-30) auto rem(-30) -140px;
    width: calc(100% + 260px); } }

.network-3 {
  margin: auto;
  @include responsiveImg('network3', 682, 512); }

.network-4 {
  margin: rem(-50) auto 0;
  @include responsiveImg('network4', 1188, 594);
  @include sm {
    margin-top: 0;
    margin-left: -430px;
    width: calc(100% + 600px); } }

.dndarea {
  margin: rem(-40) auto 0;
  @include responsiveImg('dndarea2', 1072, 721);
  @include sm {
    margin-top: rem(-50);
    margin-left: -140px;
    width: calc(100% + 260px); } }

%arrow {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: 1; }

.arrow {
  @extend %arrow;
  svg {
    display: block; }
  @include sm {
    svg {
      max-width: 100%; } } }

.arrow-1 {
  @extend %arrow;
  @include responsiveImg('arrow1', 212, 172); }

.arrow-2 {
  @extend %arrow;
  @include responsiveImg('arrow2', 112, 252); }

.arrow-3 {
  @extend %arrow;
  @include responsiveImg('arrow3', 38, 279); }

.arrow-4 {
  @extend %arrow;
  @include responsiveImg('arrow4', 194, 198); }

.arrow-5 {
  @extend %arrow;
  @include responsiveImg('arrow5', 111, 321); }

.arrow-6 {
  @extend %arrow;
  @include responsiveImg('arrow6', 59, 284); }

.hierarchy {
  margin: rem(-80) auto 0;
  @include responsiveImg('hierarchy', 1105, 565);
  @include md {
    margin-top: rem(-30); }
  @include sm {
    margin-top: 0;
    margin-left: -340px;
    width: calc(100% + 500px); } }

.meeting {
  margin: rem(80) auto 0;
  @include responsiveImg('meeting', 916, 908, 'jpg');
  @include sm {
    width: calc(100% + 225px); } }

.app-interface-1 {
  @include responsiveImg('app_interface1', 260, 524);
  width: rem(260); }

.app-interface-2 {
  @include responsiveImg('app_interface2', 260, 524);
  width: rem(260); }

.app-interface-3 {
  @include responsiveImg('app_interface3', 260, 524);
  width: rem(260); }

.app-interface-group {
  margin-top: rem(70);
  flex-wrap: wrap;
  .step {
    margin-bottom: rem(70); }
  @include md {
    .step {
      margin-bottom: rem(50); } }
  @include r(870) {
    flex-direction: column;
    align-items: center;
    .col {
      margin-top: rem(80);
      &:first-child {
        margin-top: 0; } } } }

.people {
  margin: auto;
  @include responsiveImg('people', 965, 345); }


.fade-in {
  transition: all 0.5s ease;
  transition-property: opacity, transform;
  opacity: 0;
  &.slide-up {
    transform: translateY(10px); }
  &.animate {
    opacity: 1;
    transform: translateY(0); }
  .no-js & {
    transition: initial;
    opacity: initial;
    transform: initial; } }

