.quote {
  @include f(p-regular_italic, 14, 20);
  margin: rem(40) 0;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; }
  &--simple {
    color: $color-text-grey-darker;
    margin: 1em 0; }
  @include sm {
    font-size: inherit;
    br {
      display: none; }
    .quote__cite {
      font-size: inherit; } } }

.quote__cite {
  display: block;
  font: inherit;
  margin-top: em(15, 14);
  color: $color-text-grey-darker;
  font-style: normal; }

