.header {
  position: relative;
  margin-bottom: rem(80); }

.header--mini {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
  min-width: $min-width;
  background: $color-text-main;
  color: white;
  transform: translateY(-100%);
  transition: transform 0.35s ease;
  // box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5)
  .header__left,
  .header__login-group {
    display: flex;
    align-items: center; }
  .header__content {
    min-height: rem(30);
    padding-top: 0;
    align-items: center; }
  .header__btn-separator {
    margin: 0 rem(4); }
  .back-btn-group {
    display: none;
    padding-top: 15px;
    padding-bottom: 15px;
    margin: 0; }
  &.is-visible {
    transform: none; }
  @include sm {
    padding-left: $outer-padding-sm;
    .header__content {
      display: none;
      padding: 0; }
    .back-btn-group {
      display: block; }
    .header__btn-separator {
      width: rem(20); }
    .mini-logo-sub {
      display: none; } } }

.header--mini-hub {
  @include sm {
    .header__content {
      display: flex; }
    .back-btn-group {
      display: none; } } }


.header__bar {
  position: fixed;
  background: $color-text-main;
  height: rem(8);
  top: 0;
  right: 0;
  left: $outer-padding;
  @include md {
    left: $outer-padding-md; } }

.header__content {
  display: flex;
  justify-content: space-between;
  padding-top: rem(30); }

.header__logo {
  display: inline-block;
  @include f(m-semibold, 10, 15, 500);
  text-transform: uppercase;
  color: inherit;
  text-decoration: none; }

.header__logo-second-line {
  display: block;
  margin-top: rem(3);
  @include f(p-regular, 12, 15);
  color: $color-text-grey;
  @include sm {
    font-size: rem(14); } }

.header__text {
  margin-top: rem(30);
  font-size: rem(14);
  line-height: (20/14);
  @include sm {
    font-size: inherit; } }

.header__login-group {
  @include sm {
    display: none; } }

.header__btn-separator {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: rem(1) solid;
  width: rem(50); }

.mini-logo {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  text-decoration: none;
  color: inherit;
  @include f(m-semibold, 10, 10, 500);
  @include sm {
    .mini-logo__line {
      width: rem(20); } } }

.mini-logo__line {
  margin: 0 8px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: rem(1) solid;
  width: rem(50); }

.mini-logo-sub {
  display: inline-block;
  margin-left: rem(20);
  font-size: rem(12);
  color: $color-text-grey; }
